<template lang="pug">
  page-template
    // Insert page content here
    .l-payment-page
      .l-payment-page__header
        .page-title {{ paymentPage.pageTitle }}

        .page-subtitle {{ paymentPage.pageSubtitle }}

      .l-payment-page__plans


      .l-payment-page__plans-resume
        p {{ paymentPage.plansResume.cloud }}

        p {{ paymentPage.plansResume.access }}

      .l-payment-page__guarantee
        money-back-info

      .l-payment-page__subscription-form
        payment-form

</template>

<script>
  import OneColumnTemplate from '@/components/layout/OneColumnTemplate'
  import GuaranteeMoneyBack from '@/components/GuaranteeMoneyBack'
  import PaymentForm from '@/components/PaymentForm'

  export default {
    name: 'Payment',
    components: {
      'page-template': OneColumnTemplate,
      'money-back-info': GuaranteeMoneyBack,
      'payment-form': PaymentForm
    },
    data () {
      return {
        paymentPage: {
          pageTitle: 'Please Choose one of our Affordable Subscription Plans',
          pageSubtitle: 'Thank you for trying out PDFfiller. To use our premium features, you will need a PDFfiller subscription.',
          plansResume: {
            cloud: 'All plans include unlimited cloud storage absolutely for free',
            access: 'Access your documents from anywhere with our free PDFfiller App'
          },
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  @import "../assets/scss/_helpers";

  .l-payment-page {
    @extend %container;
  }
</style>



