// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'

import router from './router'

Vue.config.productionTip = false

import { VueForm as VueForm }  from './assets/js/vue-form'

Vue.use(VueForm, {
  inputClasses: {
    valid: 'form-control-success',
    invalid: 'form-control-error'
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})

let $currentWindowWidth = window.innerWidth;

let selector = {
  formGroup: '.form-group',
  sliderContainer: '.swiper-container',
  plansNavigation: '.l-payment-plans__navigation',
  interactiveList: '.js-interactive-list-container',
  listSwitchingToggler: '.js-interactive-list-toggler'
};

let className = {
  active: 'is-active',
  success: 'has-success',
  visible: 'is-visible',
  error: 'has-error'
};

window.onresize = onWindowResize;

function onWindowResize(e){
  $currentWindowWidth = window.innerWidth;
}

function _getAncestor(el, selector) {
  let matchesFn;

  // find vendor prefix
  ['matches','webkitMatchesSelector','mozMatchesSelector','msMatchesSelector','oMatchesSelector'].some(function(fn) {
    if (typeof document.body[fn] == 'function') {
      matchesFn = fn;
      return true;
    }
    return false;
  });

  let parent;

  // traverse parents
  while (el) {
    parent = el.parentElement;
    if (parent && parent[matchesFn](selector)) {
      return parent;
    }
    el = parent;
  }

  return null;
}

function _bindDropdownElement(selector, event) {
  let $elements = selector;
  let $event = event || 'click';

  if($elements !== null && $event === 'click') {
    Array.prototype.forEach.call($elements, function(el, i){
      el.addEventListener($event, function() {
        el.classList.toggle(className.active);
      });
    });
  }

  else if($elements !== null && $event === 'mouseover') {
    Array.prototype.forEach.call($elements, function(el, i){
      el.addEventListener($event, function() {
        el.classList.add(className.active);
      });

      el.addEventListener('mouseout', function() {
        el.classList.remove(className.active);
      });
    });
  }
}

window.onload = function(){
  let $headerUserMenu = document.querySelectorAll('.account-info');

  _bindDropdownElement($headerUserMenu);
}



