import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Payment from '@/pages/Payment'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/payment',
      name: 'Payment',
      component: Payment
    }
  ]
})



